body {
  background-color: blue;
}

.App {
  margin: 0 auto;
  text-align: center;
  width: 500px;
  color: aliceblue;
}


ul {
  margin-top: 50px; /* remove top margin */
  }


  li {
    margin-top: 20px;
    background-color: rgb(24, 24, 220);
    padding: 20px;
    list-style: none;
    font-size: 1.3rem;
    font-weight: 600;
  }

  button {
    background-color: rgb(54, 54, 227);
    font-size: 1.7rem;
    border: none;
    outline: none;
    border-radius: 5%;
    color: beige;
    margin-left: 10px;
  }

  input {
    font-size: 1.5rem;
    outline: none;
    color: blue;
  }